@import "sass/index";

.root {
  display: flex;
  flex-direction: row;

  @media (max-width: 800px) {
    flex-direction: column;
    align-items: center;
  }
}
.info {
  padding: 0 20px;
  width: 340px;
  & > :first-child {
    margin-bottom: 20px;
  }
}
.raceText {
  opacity: 0.75;
  font-size: 13px;
  line-height: 19px;
  font-weight: 500;
}
.raceBox {
  background: #b3f6aa;
  border-radius: 14px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  color: #2f2b2e;
}

.tableWrapper {
  width: 100%;
  overflow-x: auto;
}

.tableTitle {
  font-size: 25px;
  margin-left: 20px;
  font-weight: bold;
  line-height: 30px;
  letter-spacing: -1.15px;
  color: use(--template-chat-massage-textBlock-color);
}

.tCellData {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.circle {
  display: flex;
  background: use(--template-statisticsBlock-tab-avatarBackground);
  border-radius: 50%;
  margin-right: spacing(1.75);
}
.userLogo {
  width: 32px;
  height: 32px;
}

.medal {
  height: 30px;
}

.timeBox {
  display: flex;
  flex-direction: column;

  padding: 13px;
  border-radius: 8px;
  margin-bottom: 17px;
  background: use(--template-chat-massage-author-color, 0.1);

  color: use(--template-chat-massage-author-color);
}
.timeTitle {
  margin: 0;
  margin-bottom: 5;

  font-size: 12px;
  font-weight: bold;
  line-height: 15px;
  letter-spacing: 0.85px;
  text-transform: uppercase;
  color: use(--template-chat-massage-author-color);
}
.timeText {
  line-height: 100%;
  margin: 0;
}
.wageredAmount {
  color: use(--template-chat-massage-textBlock-color);
  font-size: 23px;
  font-weight: bold;
  line-height: 28px;
  letter-spacing: 1.64286px;

  & > span {
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.85px;
    opacity: 0.4;
    margin: 0 5px;
  }
}
.timer {
  width: 280px;
  display: flex;

  & > div {
    color: use(--template-chat-massage-textBlock-color);
    font-size: 23px;
    font-weight: bold;
    line-height: 28px;
    letter-spacing: 1.64px;

    & > span {
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.86px;
      opacity: 0.4;
      margin: 0 5px;
    }
  }
}

.th {
  text-align: left;
  white-space: nowrap;

  &:nth-child(2) {
    width: 10000px;
  }
}

.td {
  text-align: left;
  white-space: nowrap;
}

.money {
  white-space: nowrap;
}
